import { Dialog, DialogActions, DialogContent, DialogTitle, Button, TextField } from '@mui/material';
import { useAtom, useAtomValue } from 'jotai';
import { deleteDialogOpenAtom, isAdminAtom, selectedSeatAtom, selectedSeatLineAtom } from '../../jotai';
import styles from './index.module.scss';
import toast from 'react-hot-toast';
import { useInput } from '../../shared/hooks';
import service from '../../service';
import { encrypt, reportErrorMessage } from '../../shared/utilities';
import socket from '../../socket';
import { ADMIN_PW } from '../../shared/constants';

export const DeleteDialog = () => {
  const [open, setOpen] = useAtom(deleteDialogOpenAtom);
  const [selectedSeat, setSelectedSeat] = useAtom(selectedSeatAtom);
  const [selectedSeatLine, setSelectedSeatLine] = useAtom(selectedSeatLineAtom);
  const idAdmin = useAtomValue(isAdminAtom);
  const [pw, handleChangePw, setPw] = useInput();

  const handleOkClick = async () => {
    if (selectedSeat == null || selectedSeatLine == null) {
      return;
    }

    if (encrypt(pw) !== ADMIN_PW && encrypt(pw) !== selectedSeat.pw) {
      toast.error(' .   .', { id: '1' });
      return;
    }

    try {
      const params = {
        seat: selectedSeatLine,
        seatId: selectedSeat.id,
      };
      const result = await service.cancelReservation(params);
      const { ok, message } = result;

      if (!ok) {
        toast.error(message, { id: '2' });
      }

      if (ok) {
        socket.emit('chat', {
          ...params,
          name: '',
          pw: '',
          seat_active: result.defaultSeatActive,
        });

        setOpen(false);
        setSelectedSeat(null);
        setSelectedSeatLine(null);
        setPw('');
        toast.success(message, { id: '3' });
      }
    } catch (error) {
      reportErrorMessage(error, '4');
    }
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedSeat(null);
    setSelectedSeatLine(null);
    setPw('');
  };

  return (
    <Dialog id={styles.dialog} open={open} onClose={handleClose}>
      <DialogTitle className={styles.title}> </DialogTitle>
      <DialogContent>
        <div className={styles.textFieldContainer}>
          <TextField
            value={selectedSeat?.id ?? ''}
            className={styles.textField}
            id='seat'
            label=''
            variant='standard'
            color='success'
            fullWidth
            inputProps={{ readOnly: true }}
            helperText=' '
          />
          {idAdmin && (
            <TextField
              value={selectedSeat?.name ?? ''}
              className={styles.textField}
              id='name'
              label=' '
              variant='standard'
              color='success'
              fullWidth
              inputProps={{ readOnly: true }}
              helperText=' '
            />
          )}
          <TextField
            value={pw}
            onChange={handleChangePw}
            className={styles.textField}
            id='pw'
            label=''
            type='password'
            variant='standard'
            color='success'
            fullWidth
            helperText=' '
          />
        </div>
        <DialogActions className={styles.actions}>
          <Button variant='contained' color='success' onClick={handleOkClick}>
            
          </Button>
          <Button variant='contained' color='error' onClick={handleClose}>
            
          </Button>
        </DialogActions>
      </DialogContent>
    </Dialog>
  );
};
